<template>
  <div class="eg">
    <h2>Steps </h2>
    <h3> Basic</h3>
    <div class="eg-box">
      <div class="source">
        <ab-steps v-model="value1">
          <ab-step title=""/>
          <ab-step title=""/>
          <ab-step title=""/>
        </ab-steps>
      </div>
    </div>
    <h3> Status</h3>
    <div class="eg-box">
      <div class="source">
        <ab-steps v-model="value2" is-status>
          <ab-step v-for="(item, index) in 5" :key="index" :title="index === 3 ? '4' : ''"/>
        </ab-steps>
        <ab-button type="primary" @click.stop="nextStep2"></ab-button>
      </div>
    </div>
    <h3> Description</h3>
    <div class="eg-box">
      <div class="source">
        <ab-steps v-model="value3">
          <ab-step title=" 1" content=""/>
          <ab-step title=" 2" content=""/>
          <ab-step title=" 3" content=""/>
          <ab-step title=" 4" content=""/>
        </ab-steps>
        <ab-button type="primary" @click.stop="nextStep3"></ab-button>
      </div>
    </div>
    <h3> Space</h3>
    <div class="eg-box">
      <div class="source">
        <ab-steps v-model="value4" :space="100">
          <ab-step title=""/>
          <ab-step title=""/>
          <ab-step title=""/>
        </ab-steps>
      </div>
    </div>
    <h3> Vertical</h3>
    <div class="eg-box">
      <div class="source">
        <div style="height: 300px;">
          <ab-steps v-model="value5" direction="vertical">
            <ab-step title=""/>
            <ab-step title=""/>
            <ab-step title="" content=""/>
          </ab-steps>
        </div>
      </div>
    </div>
    <h3> Click</h3>
    <div class="eg-box">
      <div class="source">
        <ab-steps v-model="value6" is-click>
          <ab-step title=""/>
          <ab-step title=""/>
          <ab-step title=""/>
          <ab-step title=""/>
        </ab-steps>
      </div>
    </div>
    <h3> Line</h3>
    <div class="eg-box">
      <div class="source">
        <ab-steps v-model="value7" is-click is-line>
          <ab-step title=""/>
          <ab-step title=""/>
          <ab-step title="" :clickabled="false"/>
          <ab-step title=""/>
        </ab-steps>
      </div>
    </div>
    <h3> Status</h3>
    <div class="eg-box">
      <div class="source">
        <ab-steps v-model="value8" is-status status="error">
          <ab-step/>
          <ab-step/>
          <ab-step/>
          <ab-step/>
        </ab-steps>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'egSteps',
  data () {
    return {
      value1: 1,
      value2: 1,
      value3: 1,
      value4: 1,
      value5: 1,
      value6: 1,
      value7: 1,
      value8: 1
    }
  },
  methods: {
    /**
     * 
     */
    nextStep2 () {
      let self = this
      if (self.value2 > 4) {
        self.value2 = 0
      } else {
        self.value2++
      }
    },
    /**
     * 
     */
    nextStep3 () {
      let self = this
      if (self.value3 > 2) {
        self.value3 = 0
      } else {
        self.value3++
      }
    }
  }
}
</script>
