<template>
  <section class="ab-container" :class="{ 'is-vertical': isVertical }">
    <slot></slot>
  </section>
</template>
<script>
export default {
  name: 'AbContainer',
  componentName: 'AbContainer',
  props: {
    direction: { //  horizontal: , vertical: 
      type: String
    }
  },
  computed: {
    /**
     * 
     * @return { Boolean } 
     */
    isVertical () {
      let self = this
      if (self.direction === 'vertical') { // 
        return true
      } else if (self.direction === 'horizontal') {
        return false
      }
      return self.$slots && self.$slots.default // , ; , , 
        ? self.$slots.default.some(vnode => {
          const tag = vnode.componentOptions && vnode.componentOptions.tag
          return tag === 'ab-header' || tag === 'ab-footer'
        })
        : false
    }
  }
}
</script>
