<template>
  <div
    class="ab-steps"
    :class="[
       `ab-steps--${direction}`
    ]">
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: 'AbSteps',
  componentName: 'AbSteps',
  data () {
    return {
      steps: [], // 
      stepOffset: 0
    }
  },
  watch: {
    steps (val) { // 
      val.forEach((child, index) => {
        child.index = index
      })
    }
  },
  props: {
    space: [Number, String], // 
    value: {}, // 
    direction: { // 
      type: String,
      default: 'horizontal'
    },
    isStatus: Boolean, // 
    status: String, //  success: ; error: 
    isClick: Boolean, // 
    isLine: Boolean // 
  }
}
</script>
