<template>
  <span
    class="ab-tag"
    :class="[
      { 'is-message': status === 'message' },
      { 'is-success': status === 'success' },
      { 'is-warning': status === 'warning' },
      { 'is-danger': status === 'danger' },
      { 'is-end': status === 'end' },
      { 'is-dot': type === 'dot' || type === 'borderdot' },
      { 'is-borderdot': type === 'borderdot' }
    ]">
    <i v-if="type === 'dot' || type === 'borderdot'" class="ab-tag__dot"></i>
    <slot></slot>
  </span>
</template>
<script>
export default {
  name: 'AbTag',
  componentName: 'AbTag',
  props: {
    status: String, // tag  message: ; success: ; warning: ; danger: ; end: ;
    type: String // tag  normal: ; dot: ; borderdot: ;
  }
}
</script>
