<template>
  <div
    class="ab-step"
    :class="[
      { 'is-pass': model > index },
      { 'is-now': model === index },
      { 'is-success': isSuccess },
      { 'is-error': isError },
      { 'is-click': $parent.isClick && clickabled },
      isLast && !space && 'is-flex',
      `is-${$parent.direction}`,
      { 'is-line': isLine }
    ]"
    :style="styles">
    <div class="ab-step__head">
      <div class="ab-step__head--line"></div>
      <div class="ab-step__head--icon" @click.stop="changeActive('icon')">
        <i v-if="isSuccess" class="ab-icon-check"></i>
        <i v-else-if="isError" class="ab-icon-close"></i>
        <span v-else>{{ index + 1 }}</span>
      </div>
    </div>
    <div class="ab-step__main">
      <div
        class="ab-step__main--title"
        :class="[
          { 'is-titleclick': isLine && $parent.isClick && clickabled }
        ]"
        @click.stop="changeActive('title')">
        {{ title || statusText }}
      </div>
      <div class="ab-step__main--content">{{ content }}</div>
    </div>
  </div>
</template>
<script>
import Emitter from 'adbright-ui/src/mixins/emitter'
export default {
  name: 'AbStep',
  componentName: 'AbStep',
  mixins: [Emitter],
  data () {
    return {
      index: -1
    }
  },
  computed: {
    /**
     * 
     * @return { Boolean } 
     */
    getSteps () {
      let self = this
      let parent = self.$parent
      while (parent) { // 
        if (parent.$options.componentName !== 'AbSteps') { // 
          parent = parent.$parent
        } else { // 
          self._steps = parent
          return true
        }
      }
      return false
    },
    /**
     * 
     */
    model: {
      get () {
        let self = this
        return self.getSteps ? self._steps.value : ''
      },
      set (val) {
        let self = this
        if (self.getSteps) {
          self.dispatch('AbSteps', 'input', [val])
        }
      }
    },
    /**
     * 
     * @return { Number } 
     */
    total () {
      let self = this
      return self.$parent.steps.length
    },
    /**
     * 
     */
    space () {
      let self = this
      const { $parent: { space } } = self
      return space
    },
    /**
     * 
     * @return { Boolean } 
     */
    isLast () {
      let self = this
      return self.index === self.total - 1
    },
    /**
     * 
     * @return { Boolean } 
     */
    isSuccess () {
      let self = this
      return self.isStatus && (self.status === 'success' || self.model > self.index || (self.model === self.index && self.$parent.status === 'success'))
    },
    /**
     * 
     * @return { Boolean } 
     */
    isError () {
      let self = this
      return self.isStatus && (self.status === 'error' || (self.model === self.index && self.$parent.status === 'error'))
    },
    /**
     * 
     * @return { Boolean } 
     */
    isLine () {
      let self = this
      return self.$parent.isLine
    },
    /**
     * 
     * @return { Boolean } 
     */
    isStatus () {
      let self = this
      return self.$parent.isStatus
    },
    /**
     * 
     */
    isVertical () {
      let self = this
      return self.$parent.direction === 'vertical'
    },
    /**
     * 
     * @return { String } 
     */
    statusText () {
      let self = this
      let text = ''
      if (!self.isStatus) return text
      if (self.isSuccess) {
        text = ''
      } else if (self.isError) {
        text = ''
      } else if (self.model === self.index) {
        text = ''
      } else {
        text = ''
      }
      return text
    },
    /**
     *  
     */
    styles () {
      let self = this
      const space = (typeof self.space === 'number'
        ? self.space + 'px'
        : self.space
          ? self.space
          : `${100 / (self.total - 1)}%`)
      let style = {}
      if (self.isLine) return style // 
      if (self.isVertical) { // 
        style.flexBasis = space
        return style
      }
      if (self.isLast) { // 
        style.maxWidth = 100 / self.total + '%'
      } else {
        style.flexBasis = space
      }
      return style
    }
  },
  props: {
    title: String, // 
    content: String, // 
    status: String, //  success: ; error: 
    clickabled: { // 
      type: Boolean,
      default: true
    }
  },
  methods: {
    /**
     * 
     * @param { String } type 
     */
    changeActive (type) {
      let self = this
      if (type === 'title' && !self.isLine) return false // 
      if (self.$parent.isClick && self.clickabled) { // 
        self.model = self.index
      }
    }
  },
  beforeCreate () { // 
    let self = this
    self.$parent.steps.push(self)
  },
  beforeDestroy () { // 
    let self = this
    const steps = self.$parent.steps
    const index = steps.indexOf(self) // 
    if (index >= 0) { // 
      steps.splice(index, 1)
    }
  }
}
</script>
