<template>
  <div class="ab-breadcrumb">
    <span class="ab-breadcrumb__item" v-for="(crumb, index) in crumbs" :key="index">
      <a
        class="is-link"
        v-if="crumb.link && crumb.target !== 'vue-router' && target !== 'vue-router'"
        :href="crumb.link"
        :target="target || crumb.target">
        {{ crumb.name }}
      </a>
      <router-link
        class="is-link"
        v-else-if="crumb.link && (crumb.target === 'vue-router' || target === 'vue-router')"
        :to="crumb.link">
        {{ crumb.name }}
      </router-link>
      <span v-else>{{ crumb.name }}</span>
      <span class="ab-breadcrumb__separator">{{ separator }}</span>
    </span>
  </div>
</template>
<script>
export default {
  name: 'AbBreadcrumb',
  componentName: 'AbBreadcrumb',
  props: {
    crumbs: { //   name: ; link: ; target: avue-router  router-link 
      type: Array,
      default () {
        return []
      }
    },
    separator: { // 
      type: String,
      default: '/'
    },
    target: String // 
  }
}
</script>
