import Vue from 'vue'
import Main from './message.vue'
import { isVNode } from 'adbright-ui/src/utils/vdom'
import { getMaxZIndex } from 'adbright-ui/src/utils/dom'
let MessageConstructor = Vue.extend(Main) // VueVue.extend
let instance
let instances = [] // closemessage
let seed = 1
let zIndex = 2000
const Message = function (options) {
  zIndex = getMaxZIndex()
  if (Vue.prototype.$isServer) return
  options = options || {}
  if (typeof options === 'string') { // 
    options = {
      message: options // messagemessage.vuedatamessage
    }
  }
  // userOnClose,  message 
  let userOnClose = options.onClose
  let id = 'message_' + seed++

  options.onClose = function () {
    Message.close(id, userOnClose)
  }
  Message.closeAll()
  instance = new MessageConstructor({ // contentinstance
    data: options // message.vuedata
  })
  instance.id = id
  // instance.message
  if (isVNode(instance.message)) {
    instance.$slots.default = [instance.message]
    instance.message = null
  }
  instance.$mount() // domVue
  document.body.appendChild(instance.$el) // dombody
  instance.verticalOffset = options.offset || 20
  instance.visible = true // message.vuevisible,message
  instance.$el.style.zIndex = zIndex++ // zindex+1
  instances.push(instance)
  return instance
}

let messageList = ['success', 'warning', 'info', 'error']
messageList.forEach(type => { // element-uiMessage
  Message[type] = options => {
    if (typeof options === 'string') {
      options = {
        message: options
      }
    }
    options.type = type
    return Message(options)
  }
})

Message.close = function (id, userOnClose) { // closeonClose
  let len = instances.length
  let index = -1
  for (let i = 0; i < len; i++) {
    if (id === instances[i].id) { // idmessage
      index = i
      if (typeof userOnClose === 'function') {
        userOnClose(instances[i])
      }
      instances.splice(i, 1) // message
      break
    }
  }
  if (len <= 1 || index === -1 || index > instances.length - 1) return
  const removedHeight = instances[index].$el.offsetHeight
  for (let i = index; i < len - 1; i++) {
    let dom = instances[i].$el
    dom.style['top'] =
      parseInt(dom.style['top'], 10) - removedHeight - 16 + 'px'
  }
}

Message.closeAll = function () { // 
  for (let i = instances.length - 1; i >= 0; i--) {
    instances[i].close()
  }
}

export default Message
