<template>
  <transition name="ab-message-fade" @after-leave="handleAfterLeave">
    <div
      :class="[
        'ab-message',
        type && !iconClass ? `ab-message--${type}` : '',
        center ? 'is-center' : '',
        showClose ? 'is-closable' : '',
        customClass
      ]"
      :style="positionStyle"
      v-show="visible"
      @mouseenter="clearTimer"
      @mouseleave="startTimer"
      role="alert">
      <i :class="iconClass" v-if="iconClass"></i>
      <i :class="typeClass" v-else></i>
      <slot>
        <p v-if="!dangerouslyUseHTMLString" class="ab-message__content">{{ message }}</p>
        <p v-else v-html="message" class="ab-message__content"></p>
      </slot>
      <i v-if="showClose" class="ab-message__closeBtn ab-icon-close" @click="close"></i>
    </div>
  </transition>
</template>
<script>
export default {
  name: 'AbMessage',
  componentName: 'AbMessage',
  data () {
    return {
      typeMap: { // 
        success: 'success',
        info: 'information',
        warning: 'warning',
        error: 'danger'
      },
      visible: false, // 
      message: '', // 
      duration: 3000, // ,
      type: 'info', // 
      iconClass: '', //  type
      customClass: '', // 
      onClose: null, // 
      showClose: false, // icon
      closed: false, // message
      verticalOffset: 20, // 
      timer: null, // 
      dangerouslyUseHTMLString: false, // html
      center: false // 
    }
  },
  computed: {
    /**
     * ,type
     */
    typeClass () {
      let self = this
      return self.type && !self.iconClass
        ? `ab-message__icon ab-icon-${self.typeMap[self.type]}`
        : ''
    },

    /**
     * 
     */
    positionStyle () {
      let self = this
      return {
        'top': `${self.verticalOffset}px`
      }
    }
  },
  watch: {
    closed (newVal) { // message
      let self = this
      if (newVal) {
        self.visible = false
      }
    }
  },
  methods: {
    /**
     * 
     */
    handleAfterLeave () {
      let self = this
      self.$destroy(true) // 
      self.$el.parentNode.removeChild(self.$el) // 
    },

    /**
     * message
     */
    close () {
      let self = this
      self.closed = true
      if (typeof self.onClose === 'function') {
        self.onClose(self)
      }
    },

    /**
     * message
     */
    clearTimer () {
      let self = this
      clearTimeout(self.timer)
    },

    /**
     * message
     */
    startTimer () {
      let self = this
      if (self.duration > 0) {
        if (self.type === 'info' || self.type === 'success') {
          self.timer = setTimeout(() => {
            if (!self.closed) {
              self.close()
            }
          }, 1000)
        } else {
          self.timer = setTimeout(() => {
            if (!self.closed) {
              self.close()
            }
          }, self.duration)
        }
      }
    },

    /**
     * message
     */
    keydown (e) {
      let self = this
      if (e.keyCode === 27) { // esc
        if (!self.closed) {
          self.close()
        }
      }
    }
  },
  mounted () {
    let self = this
    self.startTimer()
    document.addEventListener('keydown', self.keydown)
  },
  beforeDestroy () {
    let self = this
    document.removeEventListener('keydown', self.keydown)
  }
}
</script>
