<template>
  <div class="eg">
    <h2>Message </h2>
    <h3></h3>
    <div class="eg-box">
      <div class="source">
        <div class="eg-message">
          <ab-button @click="openmessage"></ab-button>
          <ab-button @click="openVn">VNode</ab-button>
        </div>
      </div>
    </div>
    <h3></h3>
    <div class="eg-box">
      <div class="source">
        <div class="eg-message">
          <ab-button @click="opensuccess"></ab-button>
          <ab-button @click="openwarning"></ab-button>
          <ab-button @click="openmessage"></ab-button>
          <ab-button @click="openerror"></ab-button>
        </div>
      </div>
    </div>
    <h3></h3>
    <div class="eg-box">
      <div class="source">
        <div class="eg-message">
          <ab-button @click="opensuccessClose"></ab-button>
          <ab-button @click="openwarningClose"></ab-button>
          <ab-button @click="openmessageClose"></ab-button>
          <ab-button @click="openerrorClose"></ab-button>
        </div>
      </div>
    </div>
    <h3></h3>
    <div class="eg-box">
      <div class="source">
        <div class="eg-message">
          <ab-button @click="openCenter"></ab-button>
        </div>
      </div>
    </div>
    <h3>HTML</h3>
    <div class="eg-box">
      <div class="source">
        <div class="eg-message">
          <ab-button @click="openHtml">HTML</ab-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'egMessage',
  methods: {
    /**
     * 
     */
    openmessage () {
      let self = this
      self.$message({
        message: ''
      })
    },

    /**
     * VNode
     */
    openVn () {
      let self = this
      const h = self.$createElement
      self.$message({
        message: h('p', null, [
          h('span', null, ' '),
          h('i', { style: 'color: teal' }, 'VNode')
        ])
      })
    },

    /**
     * 
     */
    opensuccess () {
      let self = this
      self.$message({
        message: '',
        type: 'success'
      })
    },

    /**
     * 
     */
    openwarning () {
      let self = this
      self.$message({
        message: '',
        type: 'warning'
      })
    },

    /**
     * 
     */
    openerror () {
      let self = this
      self.$message({
        message: '',
        type: 'error'
      })
    },

    /**
     * 
     */
    opensuccessClose () {
      let self = this
      self.$message({
        message: '',
        type: 'success',
        showClose: true
      })
    },

    /**
     * 
     */
    openwarningClose () {
      let self = this
      self.$message({
        message: '',
        type: 'warning',
        showClose: true
      })
    },

    /**
     * 
     */
    openerrorClose () {
      let self = this
      self.$message({
        message: '',
        type: 'error',
        showClose: true
      })
    },

    /**
     * 
     */
    openmessageClose () {
      let self = this
      self.$message({
        message: '',
        showClose: true
      })
    },

    /**
     * 
     */
    openCenter () {
      let self = this
      self.$message({
        message: '',
        center: true
      })
    },

    /**
     * Html
     */
    openHtml () {
      let self = this
      self.$message({
        message: '<i>HTML</i> <strong></strong>',
        dangerouslyUseHTMLString: true
      })
    }
  }
}
</script>
<style lang="scss">
  .eg-message {
    margin-bottom: 15px;
    width: 480px;
    &:last-child {
      margin-bottom: 0;
    }
  }
</style>
