export default {
  name: 'AbCol',
  props: {
    span: { // 
      type: Number,
      default: 24
    },
    tag: { // 
      type: String,
      default: 'div'
    },
    offset: Number, // 
    pull: Number, // 
    push: Number, // 
    xs: [Number, Object], // < 768px 
    sm: [Number, Object], //  768px 
    md: [Number, Object], //  992px 
    lg: [Number, Object], //  1200px 
    xl: [Number, Object] //  1920px 
  },

  computed: {
    /**
     * 
     * @param { Number } 
     */
    gutter () {
      let parent = this.$parent
      while (parent && parent.$options.componentName !== 'AbRow') { //  'AbRow' , 
        parent = parent.$parent
      }
      return parent.gutter ? parent.gutter : 0 //  gutter , ,  0
    }
  },
  /**
   * 
   * @param { Function } h 
   */
  render (h) {
    let classList = []
    let style = {}
    if (this.gutter) { // 
      style.paddingLeft = this.gutter / 2 + 'px' //   / 2 
      style.paddingRight = style.paddingLeft // 
    }
    let label = ['span', 'offset', 'pull', 'push'] // 
    label.forEach(prop => { // 
      if (this[prop] || this[prop] === 0) { //    0
        classList.push(
          prop !== 'span'
            ? `ab-col-${prop}-${this[prop]}`
            : `ab-col-${this[prop]}`
        )
      }
    })
    let config = ['xs', 'sm', 'md', 'lg', 'xl']
    config.forEach(size => { // 
      if (typeof this[size] === 'number') {
        classList.push(`ab-col-${size}-${this[size]}`)
      } else if (typeof this[size] === 'object') {
        let props = this[size]
        Object.keys(props).forEach(prop => {
          classList.push(
            prop !== 'span'
              ? `ab-col-${size}-${prop}-${props[prop]}`
              : `ab-col-${size}-${props[prop]}`
          )
        })
      }
    })
    return h(this.tag, { // 
      class: ['ab-col', classList],
      style // 
    }, this.$slots.default)
  }
}
