<template>
  <div
    class="ab-radio-group"
    :class="{'is-vertical': vertical}">
    <slot></slot>
  </div>
</template>
<script>
import Emitter from 'adbright-ui/src/mixins/emitter'
export default {
  name: 'AbRadioGroup',
  componentName: 'AbRadioGroup',
  inject: {
    abFormItem: {
      default: ''
    }
  },
  mixins: [Emitter],
  props: {
    value: {}, // 
    disabled: Boolean, // 
    vertical: Boolean // 
  },
  created () { // 
    let self = this
    self.$on('propChange', val => { //  change  change 
      self.$emit('change', val)
    })
  },
  watch: {
    value (val) {
      let self = this
      self.dispatch('AbFormItem', 'ab.form.change', [self.value])
    }
  }
}
</script>
